<template>
  <v-dialog
    v-model="dialog"
    width="1000"
  >
    <v-card>
      <v-card-title>
        <span class="text-h6">Create a Test</span>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pt-3 pb-6">
        <v-alert
          :value="!!createError"
          dense
          outlined
          type="error"
          transition="scale-transition"
          icon="mdi-exclamation-thick"
          class="mb-3"
        >
          {{ createError }}
        </v-alert>
        <v-form
          ref="form"
          v-model="valid"
          @submit.prevent="handleSubmit"
        >
          <v-row>
            <v-col
              cols="12"
            >
              <v-text-field
                ref="test_name"
                v-model="test_name"
                :rules="[rules.required, rules.maximum(300)]"
                label="Test Name"
                counter="300"
                maxlength="300"
                hide-details="auto"
              ></v-text-field>
            </v-col>
            <v-col>
              <tiptap-vuetify
                v-model="content"
                :extensions="extensions"
              />
            </v-col>
          </v-row>
          <div class="mt-5">
            <v-btn
              :disabled="!valid"
              type="submit"
              color="blue darken-3 white--text"
              class="mr-2 mt-2"
              :loading="createLoading"
              depressed
            >
              Create
            </v-btn>
            <v-btn
              @click="closeModal"
              color="secondary"
              class="mt-2"
              depressed
              outlined
            >
              Cancel
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex"
import formRules from '@/helpers/formRules'
import { TiptapVuetify, Heading, Bold, Italic, Strike, Underline, Code, Paragraph, BulletList, OrderedList, ListItem, Link, Blockquote, HardBreak, HorizontalRule, History } from 'tiptap-vuetify'

export default {
  components: { TiptapVuetify },
  props: {
    modalData: {
      type: Object,
      required: true
    }
  },
  data: () => ({
    valid: true,
    test_name: '',
    rules: formRules,
    extensions: [
      History,
      Blockquote,
      Link,
      Underline,
      Strike,
      Italic,
      ListItem,
      BulletList,
      OrderedList,
      [Heading, {
        options: {
          levels: [1, 2, 3]
        }
      }],
      Bold,
      Code,
      HorizontalRule,
      Paragraph,
      HardBreak
    ],
    content: `
      Yay Headlines!
      
    `
  }),
  computed: {
    ...mapState("tests", ["createLoading", "createError"]),
    dialog: {
      get: function () {
        return this.modalData.dialog
      },
      // setter
      set: function () {
        this.closeModal()
      }
    },
  },
  mounted() {
    this.$refs.test_name.focus()
  },
  methods: {
    ...mapActions("tests", ["createRecord"]),
    async handleSubmit () {
      const formData = { test_name: this.test_name, textarea: this.content }

      console.log(formData)

      /* if (!this.createLoading) {
        const res = await this.createRecord({ test_name: this.test_name })

        if (!this.createError) {
          // reset form
          this.$refs.form.reset()
          this.closeModal()

          this.$router.push({ name: "TestSettings", params: { id: res.id } })
        }
      } */
    },

    closeModal() { 
      this.$emit('closeModal')
    }
  },
}
</script>