<template>
  <v-container fluid class="px-5">
    <v-row class="mb-3" justify="space-between">
      <v-col cols="12" sm="6" md="4" xl="2" align-self="end">
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              color="blue darken-3 white--text"
              @click="showAddModal"
              v-bind="attrs"
              v-on="on"
              large
              class="mr-1"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </template>
          <span>Create a Test</span>
        </v-tooltip>
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              color="secondary"
              @click="loadRecords"
              v-bind="attrs"
              v-on="on"
              class="mr-1"
            >
              <v-icon>mdi-cached</v-icon>
            </v-btn>
          </template>
          <span>Refresh Questions</span>
        </v-tooltip>
      </v-col>
      <v-col cols="12" sm="6" md="4" xl="3">
        <v-text-field
          v-model="search"
          prepend-inner-icon="mdi-magnify"      
          label="Search"
          single-line
          hide-details="auto"
          :disabled="recordsLoading"
          clearable
        >
        </v-text-field>
      </v-col>
    </v-row>
    <v-data-table
      :headers="headers"
      :items="records"
      item-key="id"
      class="elevation-1"
      :search="search"
      :loading="recordsLoading"
      loading-text="Loading... Please wait"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
    >
      <template v-slot:item.actions="{ item }">
        <v-tooltip right>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              :to="{ name: 'TestSettings', params: { id: item.id } }"
              icon
              small
            >
              <v-icon
                size="large"
                color="blue darken-3"
              >
                mdi-cog
              </v-icon>
            </v-btn>
          </template>
          <span>Settings</span>
        </v-tooltip>
      </template>
    </v-data-table>
    <AddModal v-if="addModal" :modalData="addModalData" @closeModal="addModal = false" @refresh="loadRecords" />
  </v-container>
</template>

<script>
import { mapState, mapActions } from "vuex"
import AddModal from "@/components/applicantTests/AddModal"

export default {
  components: { AddModal },
  data: () => ({
    search: '',
    sortBy: 'created_at',
    sortDesc: true,
    headers: [
      { 
        text: 'Action', 
        value: 'actions', 
        sortable: false,
        align: 'center'
      },
      { text: 'Name', value: 'name' },
      { text: 'Created At', value: 'created_at' },
      { text: 'Updated At', value: 'updated_at' },
    ],
    addModal: false,
    addModalData: [],
  }),
  created() {
    this.loadRecords()
  },
  computed: {
    ...mapState("tests", ["records", "recordsLoading"]),
  },
  methods: {
    ...mapActions("tests", ["getRecords"]),
    loadRecords() {
      if (!this.recordsLoading) {
        this.$store.commit("tests/setRecords", [])
        this.getRecords()
      }
    },
    showAddModal() {
      this.addModal = true
      this.addModalData = { dialog: this.addModal }
    },
  }
}
</script>